.toggleButton{
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    padding: 10px;
    align-items: center;
}
.toggleButton__button{
    all:unset;
    padding:.15em 1em;
    background-color: red;
    color:white;
    font-size: 2em;
    cursor: pointer;
    border-radius: 10px;
}
.toggleButton__button:active{
    transform: scale(90%, 90%);
}
.toggleButton__button--green{
    background-color: green;
}